h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}
button {
  padding: 0;
  outline: none;
  cursor: pointer;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
svg {
  display: block;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  display: flex;
}

a {
  color: currentColor;
  outline: none;
  text-decoration: none;
}

* {
  -webkit-tap-highlight-color: transparent;
}
