.loader {
  z-index: 1001;
  opacity: 1;
  visibility: visible;
  justify-content: center;
  align-items: center;
  width: 20em;
  height: 20em;
  font-size: 10px;
  display: flex;
  position: relative;
}

.loader.done {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.loader .face {
  border-style: solid;
  border-radius: 50%;
  animation: 3s linear infinite animate;
  position: absolute;
}

.loader .face:first-child {
  color: #f80;
  --deg: -45deg;
  border-width: .2em .2em 0 0;
  border-color: currentColor #0000 #0000 currentColor;
  width: 100%;
  height: 100%;
  animation-direction: normal;
}

.loader .face:nth-child(2) {
  color: gold;
  --deg: -135deg;
  border-width: .2em 0 0 .2em;
  border-color: currentColor currentColor #0000 #0000;
  width: 70%;
  height: 70%;
  animation-direction: reverse;
}

.loader .face .circle {
  transform: rotate(var(--deg));
  transform-origin: 0;
  background-color: #0000;
  width: 50%;
  height: .1em;
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader .face .circle:before {
  content: "";
  background-color: currentColor;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  position: absolute;
  top: -.5em;
  right: -.5em;
  box-shadow: 0 0 2em, 0 0 4em, 0 0 6em, 0 0 8em, 0 0 10em, 0 0 0 .5em #ffff001a;
}

